import { Component, OnInit } from '@angular/core';
import { AngularFire } from 'angularfire2';
import {FirebaseService} from '../../../services/firebase.service';
import {Router, ActivatedRoute, Params} from '@angular/router';
import * as firebase from 'firebase';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.sass']
})
export class EditProfileComponent implements OnInit {
  uid: any;
  promoUniq: any; // Uniq promo code
  options: any; // profile styles options
  // Edit profile info
  profile;
  profileUid: any;
  name;
  username;
  displayName
  image;
  description;
  promo;
  userin: any;
  styles = [
    { id: '0', display: 'StreetDance', checked: false},
    { id: '1', display: 'Modern', checked: false},
    { id: '2', display: 'Ballet', checked: false},
    { id: '3', display: 'Jazz', checked: false},
  ];

  // Current Profile lets
  supportingAdd: any;
  supportUrl: any;
  supportUser: any;
  supportKey: any;
  profiles: any;

  //  other
  id;
  currentUser: any;
  user: any;
  sub: boolean;

  constructor(
    private firebaseService:FirebaseService,
    private router: Router,
    private route: ActivatedRoute,
    private af: AngularFire,
  ) { }

  ngOnInit() {
    // Get LoggedIn id
    this.id = this.route.snapshot.params['username'];

    // Store the current user EMAIL
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });

    // LOGGEDIN Profile INFORMATION
    this.firebaseService.getProfileDetails(this.id).subscribe(profile => {

      this.profile = profile;
      this.options = profile.styles;
      if (profile.userin !== this.currentUser) {
        this.router.navigate(['/']);
      }
      this.username = profile.username;
      this.styles = profile.styles
      this.profileUid = profile.userin
      this.userin = profile.$key;
      this.description = profile.description;
      this.displayName = profile.displayName;
      this.promo = profile.promo;
      this.uid = profile.uid;
    });

  }
  checkPromo(promo) {
    this.firebaseService.getProfilePromo(promo).subscribe(profiles => {
      this.promoUniq = profiles[0]

    })
  }
  styleFalse(style) {
    let styles = {
      checked: false
    }
    this.firebaseService.updateProfile(this.id+'/styles/'+style, styles)

  }
   styleTrue(style) {
    let styles = {
      checked: true
    }
    this.firebaseService.updateProfile(this.id+'/styles/'+style, styles)

  }

  onEditSubmit() {

    let profile = {
      username: this.username,
      description: this.description,
      displayName: this.displayName,
      promo: this.promo
    }
    let video = {
      username: this.username,
    }
    let styles = {
      styles: this.styles
    }
    this.firebaseService.updateProfile(this.id, profile);
    this.firebaseService.updateProfile(this.id, styles);

    this.router.navigate(['/'+this.id])

  }
  onDeleteClick(path){
    // DELETE SUPPORT AND SUPPORTING FROM PROFILES
    this.firebaseService.getProfiles().subscribe(profiles => {

      for (var i = 0; i < profiles.length; i++) {
          // 1. Take 1 PROFILES support
          let takeSupport = {
            supporters: profiles[i].supporters - 1
          }
          // 2. Take 1 PROFILES supporting
          let takeSupporting = {
            supportingAdd: profiles[i].supportingAdd - 1
          }
          // 3. Update support with true
          let sub = {
            support: true
          }
          if(profiles[i].support[this.id] != undefined && profiles[i].support[this.id]) {
            this.firebaseService.updateProfile(profiles[i].$key, takeSupport);
            this.firebaseService.deleteProfile(profiles[i].$key+'/support/'+this.id);
            this.firebaseService.updateProfile(profiles[i].$key+'/support/', sub);
          } else if(profiles[i].supporting[this.id] != undefined && profiles[i].supporting[this.id]) {
              this.firebaseService.updateProfile(profiles[i].$key, takeSupporting);
          } else {
              this.firebaseService.deleteProfile(this.id);
          }
      }
    })
    // DELETE SUPPORT AND SUPPORTING FROM SUPPORTS
    this.firebaseService.getSupporters().subscribe(supporters => {
      for (var i = 0; i < supporters.length; i++) {
          if(supporters[i][this.id] != undefined && supporters[i][this.id]) {
            this.firebaseService.deleteSupport(supporters[i].$key+'/'+this.id);
          } else {
             // DELETE SUPPORT PROFILE
              this.firebaseService.deleteSupport(this.username);
          }
      }
    })


    // DELETE Image from storage
    let storageRef = firebase.storage().ref();
    let spaceRef = storageRef.child(path);
      storageRef.child(path).delete().then((url) => {
      }).catch((error) => {
        console.log(error);
      });
    // DELETE PROFILE && USER
    this.firebaseService.deleteUser(this.uid);
    this.firebaseService.deleteProfile(this.userin);
    this.af.auth.logout();
    this.router.navigate(['/']);
    return this.currentUser = '';
  }
}



