import { Component, OnInit } from '@angular/core';
import { FirebaseService } from "app/services/firebase.service";
import { AngularFire } from "angularfire2";
import { ActivatedRoute } from "@angular/router";

import * as firebase from 'firebase';
import { CrewService } from "app/services/crew.service";

@Component({
  selector: 'app-classes',
  templateUrl: './classes.component.html',
  styleUrls: ['./classes.component.sass']
})
export class ClassesProfileComponent implements OnInit {
  class: any;
  userUrl: any;
  userSupporters: any;
  userKey: any;
  profiles: any;
  schedules: any;
  cls: any;
  classes: any;
  profile: any;
  currentUser: string;
  id: any;
  constructor(
      private firebaseService: FirebaseService,
      private crewService: CrewService,
      private af: AngularFire,
      private route:ActivatedRoute
  ) {
   }
  ngOnInit() {
        // Get id to fetch the user details
    this.id = this.route.snapshot.params['username'];
    // Store the current user
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });
       // Get Current Profile to Display
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.userKey = profiles[i].$key;
          this.userSupporters = profiles[i].supporters;
          this.userUrl = profiles[i].url;

        }
      }
    });
      // Get Profile Details
    this.firebaseService.getProfileDetails(this.id).subscribe(profile => {
      this.profile = profile;

    });
     // Get Class Details
    this.crewService.getClass(this.id).subscribe(cls => {
      this.class = cls[0];
    });
             // Get Classes to Display
    this.crewService.getClasses().subscribe((classes) => {
      this.classes = classes;
    });
            // Get Classes to Display
    this.crewService.getClassDetails(this.id).subscribe((cls) => {
      this.cls = cls;
    });
  }
  sendRequest(key) {
    let request = {
      requestFrom: this.userKey
    }
     let requestCountAdd = {
      requestCount: this.class.requestCount + 1
    }

    this.crewService.updateClass(key+'/requests/'+this.userKey, request)
    this.crewService.updateClass(key, requestCountAdd)
  }
   cancelRequest(key) {
    let requestCountTake = {
      requestCount: this.class.requestCount - 1
    }
    let requestDefault = {
      requests: false
    }

    this.crewService.deleteClass(key+'/requests/'+this.userKey);
    this.crewService.updateClass(key, requestCountTake)
    if(this.class.requestCount < 1) {
      this.crewService.updateClass(key, requestDefault);
    }
  }

}



