import { Component, OnInit } from '@angular/core';
import { FirebaseService } from "app/services/firebase.service";
import { AngularFire } from "angularfire2";
import * as firebase from 'firebase';
import { ActivatedRoute, Router } from "@angular/router";
import { TruncatePipe } from 'app/pipes/truncate.pipe';
@Component({
  selector: 'app-browse',
  templateUrl: './browse.component.html',
  styleUrls: ['./browse.component.sass']
})
export class BrowseComponent implements OnInit {
  zeals: any[];
  videoClick: any;
  newDate: any;
  count: any;
  duration: any;
  sub: boolean = false;
  comments: any[];
  currentUrl: any;
  currentKey: any;
  currentUsername: any;
  date: Date = new Date();
  videoFalse: any;
  profiles: any[];
  fixed: boolean;
  profile: any;
  id: any;
  currentUser: string;
  videos: any[];
  comment;
  appState: string;
  constructor(
      private firebaseService: FirebaseService,
      private af: AngularFire,
      private route:ActivatedRoute,
      private router: Router
  ) {
   }

  ngOnInit() {
    // Get id to fetch the user details
    this.id = this.route.snapshot.params['username'];
    // Get Videos to Display
    this.firebaseService.getZeals().subscribe((zeals) => {
      this.zeals = zeals;
    });
    // Store the current user
    this.af.auth.subscribe(auth => {

     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });
    // Get Current Profile to Display
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.currentKey = profiles[i].$key;
          this.currentUrl = profiles[i].url;
        }
      }
    });
   // Get Videos to Display
    this.firebaseService.getVideos().subscribe((videos) => {
      this.videos = videos;
    });
  }
  navigateTo(value) {
      if (value === 'popular') {
          this.router.navigate(['/browse/'+value]);
      } else if(value === 'filter') {
            this.router.navigate(['/browse/']);
      }
      return false;
  }
  updateFalse(key, count, views) {
     let videoTrue = {
      watch: true,
    }
    let zealTrue = {
      zealState: true,
    }
    let watch = {
      watching: count + 1,
      views: views + 1

    }
    let unwatch = {
      watching: this.count - 1
    }
    let videoFalse = {
      watch: false
   }
     let zealFalse = {
      zealState: false,
    }
   if(this.count > 1 ) {
     this.firebaseService.updateVideo(this.videoFalse, unwatch);
     this.firebaseService.deleteVideo(this.videoFalse+'/watch/'+this.currentKey+'/watch')
    this.firebaseService.deleteZeal(this.videoFalse+'/zealState')
   } else if(this.count === 1) {
      this.firebaseService.updateVideo(this.videoFalse, unwatch);
      this.firebaseService.updateVideo(this.videoFalse, videoTrue)
      this.firebaseService.updateZeal(this.videoFalse+'/', zealTrue)
   }
   this.firebaseService.updateVideo(key+'/watch/'+this.currentKey, videoFalse)
  this.firebaseService.updateZeal(key+'/', zealFalse)
  this.firebaseService.updateVideo(key, watch)
  }
}



