import { Component, OnInit } from '@angular/core';
import { FirebaseService } from "app/services/firebase.service";
import { AngularFire } from "angularfire2";
import * as firebase from 'firebase';
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-edit-videos',
  template: `
              <br>
                <div *ngFor="let video of videos; let i=index;">
                  <div class="col-md-2 " *ngIf="video?.userin === profile?.userin && !video?.watch[currentKey]">

                      <div>
                          <div class="positionRelative">
                            <button class="btn pull-right modifiedEditBtn" [routerLink]="['/video-edit/'+video.$key]">Edit</button>
                          </div>
                          <video width="100%" height="auto" src="{{video?.url}}"></video>
                          <div class="positionRelative">
                              <p class="modifiedBtn">{{video?.duration / 100 | number: '1.2-2'}}</p>
                          </div>

                      </div>

                      <div class="minH">
                          <div class="col-md-12 ">
                              <h4>{{video?.name | truncate : 50}}</h4>
                          </div>
                          <div class="row">
                              <div class="inline-block">
                                  <p>by:&nbsp;<a [routerLink]="['/'+video?.username]">@{{video?.username}}</a></p>
                                  <p>&nbsp;  &nbsp;<span class="glyphicon glyphicon-globe"></span><span class="glyphicon glyphicon-globe "></span><span class="glyphicon glyphicon-globe"></span><span class="pull-right">&nbsp;  &nbsp;</span></p>
                                  <p><span class="glyphicon glyphicon-globe pull-right"></span></p>
                              </div>
                          </div>
                           <div class="row text-left">
                              <div class="inline-block">
                                  <p>{{zeals[i]?.zeals}} zeals</p>
                                  <p>&nbsp;  &nbsp; {{video?.views}} views </p>
                                  <p>&nbsp;  &nbsp;{{video?.date | toDate | timeAgo}} </p>
                              </div>
                          </div>
                      </div>
                  </div>

              </div>

            `,
  styleUrls: ['./edit-video.component.sass']
})
export class EditVideosComponent implements OnInit {
  zeals: any;
  profile: any;
  id: any;
  currentUser: string;
  videos: any[];

  constructor(
      private firebaseService: FirebaseService,
      private af: AngularFire,
      private route:ActivatedRoute
  ) { }

  ngOnInit() {
    // Get id to fetch the user details
    this.id = this.route.snapshot.params['username'];
    // Get Zeals to Display
    this.firebaseService.getZeals().subscribe((zeals) => {
      this.zeals = zeals;
    });
    // Store the current user
    this.af.auth.subscribe(auth => {

     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });
    // Get Videos to Display
    this.firebaseService.getVideos().subscribe((videos) => {
      this.videos = videos;

    });
      // Get Profile Details
    this.firebaseService.getProfileDetails(this.id).subscribe(profile => {
      this.profile = profile;

      });
  }

}



