import { Component, OnInit } from '@angular/core';
import { AngularFire } from 'angularfire2';
import {FirebaseService} from '../../../../services/firebase.service';
import {Router, ActivatedRoute, Params} from '@angular/router';
import * as firebase from 'firebase';

@Component({
  selector: 'app-edit-video',
  templateUrl: './edit-video.component.html',
  styleUrls: ['./edit-video.component.sass']
})
export class EditVideoComponent implements OnInit {
  userUid: any;
  id;
  name;
  image;
  description;
  video: any;
  currentUser: any;
  profiles: any;

  constructor(
    private firebaseService:FirebaseService,
    private router: Router,
    private route: ActivatedRoute,
    private af: AngularFire,
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params['username'];
        // Get Current Profile to Display
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.userUid = profiles[i].$key;
        }
      }
    });
    this.firebaseService.getVideoDetails(this.id).subscribe(video => {
      this.video = video;

      this.af.auth.subscribe(auth => {
       if(auth) {
         this.currentUser = firebase.auth().currentUser.email;
         if (video.userin != this.currentUser) {
           this.router.navigate(['/']);
         }
       }
       return this.currentUser;
      });

      this.name = video.name;
      this.description = video.description;
    });
  }
  onEditSubmit() {
    let video = {
      name: this.name,
      description: this.description,
    }

    this.firebaseService.updateVideo(this.id, video);
    this.router.navigate(['/video-manager/'+this.userUid])
  }
  onDeleteClick(path){
    this.firebaseService.deleteVideo(this.id);
    let storageRef = firebase.storage().ref();
    let spaceRef = storageRef.child(path);
      storageRef.child(path).delete().then((url) => {
      }).catch((error) => {
        console.log(error);
      });
    this.router.navigate(['/video-manager/'+this.userUid])
  }

}



