import { Injectable } from '@angular/core';
import { AngularFire, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2';
import * as firebase from 'firebase';
import 'rxjs/add/operator/take';

@Injectable()
export class FirebaseService {
  users: FirebaseListObservable<any[]>;

  profiles: FirebaseListObservable<any[]>;
  profile: FirebaseObjectObservable<any>;

  videos: FirebaseListObservable<any[]>;
  video: FirebaseObjectObservable<any>;

  comments: FirebaseListObservable<any[]>;
  comment: FirebaseObjectObservable<any>;

  supports: FirebaseListObservable<any[]>;
  support: FirebaseObjectObservable<any>;

  zeals: FirebaseListObservable<any[]>;
  zeal: FirebaseObjectObservable<any>;

  public userName: any;
  folder: any;
  sub: boolean = false
  vids: any;
  id: any;
  constructor(private af: AngularFire) {
    this.folder = 'image-profile';
    this.vids = 'video-profile';
  }

  // Users Methods
  getUsers(){
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.userName = firebase.auth().currentUser.uid;
       this.users = this.af.database.list('/users/'+this.userName+'/') as FirebaseListObservable<User[]>
     }
     return this.users;
   });
    this.users = this.af.database.list('/users/'+this.userName+'/') as FirebaseListObservable<User[]>
    return this.users;
  }
  addUser(user){
    return this.users.push(user);
  }

  // Profiles Methods
  getProfiles(){
    this.profiles = this.af.database.list('/profiles') as FirebaseListObservable<Profile[]>
    return this.profiles;
  }
  getProfilePromo(promo){
    this.profiles = this.af.database.list('/profiles', {
      query: {
        orderByChild: 'promo',
        equalTo: promo
      }
    }) as FirebaseListObservable<Profile[]>
    return this.profiles;
  }
  getProfileDetails(id){
    this.profile = this.af.database.object('/profiles/'+id) as FirebaseObjectObservable<Profile>
    return this.profile;
  }

  addProfile(user, profile){
    this.userName = firebase.auth().currentUser.uid;

    // Create root ref
    let storageRef = firebase.storage().ref();
    for(let selectedFile of [(<HTMLInputElement>document.getElementById('image')).files[0]]){
      let path = `/${this.folder}/${this.userName}`;
      let iRef = storageRef.child(path);
      iRef.put(selectedFile).then((snapshot) => {
        profile.image = selectedFile.name;
        profile.path = path;
        storageRef.child(path).getDownloadURL().then((url) => {
          // Set image url
          profile.url= url;
          return this.profiles.update(user, profile);

        })
      });
    }
  }

  // Videos Methods
  getVideos(){
    this.videos = this.af.database.list('/videos/', {
      query: {
       orderByChild: 'date',
      }
    }) as FirebaseListObservable<Video[]>
    return this.videos;
  }

  getProfileVideos(id){
    this.videos = this.af.database.list('/videos/'+id) as FirebaseListObservable<Video[]>
    return this.videos;
  }
  getDurationVideo(id){
    this.videos = this.af.database.list('/videos/', {
      query: {
       orderByChild: 'userUid',
        equalTo: id
      }
    }) as FirebaseListObservable<Video[]>
    return this.videos;
  }

  getVideoDetails(id: any){
    this.video = this.af.database.object('/videos/'+id).take(1) as FirebaseObjectObservable<Video>
    return this.video;
  }


  addVideo(video: any){
    let newPostKey: string = firebase.database().ref().child('videos').push().key;

    let storageRef = firebase.storage().ref();
    for(let selectedFile of [(<HTMLInputElement>document.getElementById('video')).files[0]]){
      let path = `/${this.vids}/${newPostKey}`;
      let iRef = storageRef.child(path);
      iRef.put(selectedFile).then((snapshot) => {
        video.image = newPostKey;
        video.path = path;
        storageRef.child(path).getDownloadURL().then((url) => {
          // Set image url
          video.url= url;
          return this.videos.push(video);
        })
      });
    }
  }
  // Zeals Methods
  getZeals(){
    this.zeals = this.af.database.list('/zeals/') as FirebaseListObservable<Zeal[]>
    return this.zeals;
  }
  getZealDetails(id) {
    this.zeal = this.af.database.object('/zeals/'+id) as FirebaseObjectObservable<Zeal>
    return this.zeal;
  }

  // Comment Methods
  getComments(){
    this.comments = this.af.database.list('/comments/') as FirebaseListObservable<Comment[]>
    return this.comments;
  }
   addComment(change: any){
   return this.comments.push(change)
  }
 // Support Methods
  getSupporters(){
    this.supports = this.af.database.list('/supports/') as FirebaseListObservable<Support[]>
    return this.supports;
  }

  addSupport(support) {
    return this.supports.push(support)
  }

  // Update Methods
  updateZeal(id: any, change: any) {
    this.getZeals()
    return this.zeals.update(id, change);
  }
  updateSupport(id: any, change: any) {
    this.getSupporters()
    return this.supports.update(id, change);
  }
  updateProfile(id: any, change: any) {
    this.getProfiles()
    return this.profiles.update(id, change);
  }
  updateStyles(id: any, state: any) {
    this.getProfiles()
    return this.profiles.update(id, state);
  }
  updateProfileImage(id: any, change: any) {
    this.userName = firebase.auth().currentUser.uid;
    let storageRef = firebase.storage().ref();
    for(let selectedFile of [(<HTMLInputElement>document.getElementById('image')).files[0]]){
      let path = `/${this.folder}/${this.userName}`;
      let iRef = storageRef.child(path);
      iRef.put(selectedFile).then((snapshot) => {
        change.image = selectedFile.name;
        change.path = path;
        storageRef.child(path).getDownloadURL().then((url) => {
          // Set image url
          change.url= url;
          return this.profiles.update(id, change);
        })
      });
    }
  }
  updateSupportImage(id: any, change: any) {
    let storageRef = firebase.storage().ref();
    for(let selectedFile of [(<HTMLInputElement>document.getElementById('image')).files[0]]){
      let path = `/${this.folder}/${this.userName}`;
      let iRef = storageRef.child(path);
      iRef.put(selectedFile).then((snapshot) => {
        storageRef.child(path).getDownloadURL().then((url) => {
          // Set image url
          change.url= url;
          return this.supports.update(id, change);
        })
      });
    }
  }

  updateVideo(id: any, change: any) {
    return this.videos.update(id, change);
  }
  updateUser(id: any, change: any) {
    return this.users.update(id, change);
  }
  // Delete Methods
  deleteZeal(id: any){
    this.getZeals()
    return this.zeals.remove(id)
  }
  deleteUser(id: any){
    this.getUsers()
    return this.users.remove(id)
  }
  deleteComment(id: any){
    this.getComments()
    return this.comments.remove(id)
  }
  deleteProfile(id: any){
    this.getProfiles()
    return this.profiles.remove(id)
  }
  deleteSupport(id: any){
    this.getSupporters()
    return this.supports.remove(id)
  }
  deleteVideo(id: any){
    this.getVideos()
    return this.videos.remove(id)
  }

}

interface User{
  $key?:string;
  username?: string;
}
interface Profile{
  $key?:string;
  username?: string;
  displayName?: string;
  supporters?: number;
  description?:string;
  image?:string;
}
interface Support{
  $key?:string;
  username?: string;
  supporters?: number;
}
interface Zeal{
  $key?:string;
  username?: string;
  supporters?: number;
}
interface Video{
  $key?:string;
  name?: string;
  description?: string;
}
interface Comment{
  $key?:string;
  name?: string;
  description?: string;
  url?: string;
}




