import { Component, OnInit } from '@angular/core';
import { AngularFire } from 'angularfire2';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { FirebaseService} from '../../services/firebase.service';
import { CrewService} from '../../services/crew.service';
import * as firebase from 'firebase';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.sass']
})
export class DashboardComponent implements OnInit {
  zeals: any[];
  comments: any; // Get comments
  userUid: any; // Current profile key
  classes: any[]; // classes array
  supports: any[]; //support array

  users: any; // check if users have info

  profiles: any; // get profiles
  profile: any; // get profile
  userin: any; // get user id
  videos: any; // get videos
  video: any; // get video supporters

  supportIsNull: any; // check if null
  supportState: any; // support state false or true
  sub: boolean; // support status

  currentUser: any; // get currentUser
  id: any; // params id

  constructor(
    private firebaseService: FirebaseService,
    private crewService: CrewService,
    private af: AngularFire,
    private router: Router,
    private route:ActivatedRoute
) {}

  ngOnInit() {

    // Get id to fetch the user details
    this.id = this.route.snapshot.params['username'];
    // Get current email
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });
    // Get Current Profile
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.userUid = profiles[i].$key;
          // this.username = profiles[i].username;
        }
      }
    });
    // Get Supports to Display
    this.firebaseService.getSupporters().subscribe((supports) => {
      this.supports = supports;

    });
     // Get Classes to Display
    this.crewService.getClasses().subscribe((classes) => {
      this.classes = classes;

    });
    // Get Comments to Display
    this.firebaseService.getComments().subscribe((comments) => {
      this.comments = comments;
    });
    // Get Profiles to Display
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;

    });
    // Get Zeals to Display
    this.firebaseService.getZeals().subscribe((zeals) => {
      this.zeals = zeals;
    });
    // Get Videos to Display
    this.firebaseService.getVideos().subscribe((videos) => {
      this.videos = videos;
    });

    // CHECK IF USER HAS UPLOADED INFORMATION
    this.af.auth.subscribe(auth => {
      if(auth) {
        this.firebaseService.getUsers().subscribe(users => {
          this.users = users;
          if (users.length === 0) {
            this.router.navigate(['/add']);
          } else {
            this.router.navigate(['/']);
          }
        });
      }
      return this.users;
    });
  }

}



