import { Component, OnInit, VERSION } from '@angular/core';
import { AngularFire } from 'angularfire2';
import {CrewService} from '../../../../services/crew.service';
import {Router, ActivatedRoute, Params} from '@angular/router';
import * as firebase from 'firebase';
import { FirebaseService } from "app/services/firebase.service";

@Component({
  selector: 'app-add-classes',
  templateUrl: './add-classes.component.html',
  styleUrls: ['./add-classes.component.sass']
})
export class AddClassesComponent implements OnInit {
  userClassCount: any;
  profile: any;
  userUrl: any;
  participants: number = 0;
  userEmail: any;
  classes: any[];
  profiles: any;
  userUid: any;
  videos: any;
  title: any;
  studio:any;
  username: any;
  description: any;
  level: any;
  age:any;
  location:any;
  city:any;
  phone:number;
  userin: any;
  date: Date = new Date();

  schedules = [
    { display: 'Monday', from: '', to: ''},
    { display: 'Tuesday', from: '', to: ''},
    { display: 'Wednesday', from: '', to: ''},
    { display: 'Thursday', from: '', to: ''},
    { display: 'Friday', from: '', to: ''},
    { display: 'Saturday', from: '', to: ''},
    { display: 'Sunday', from: '', to: ''},

  ];
  styles = [
    { id: '0', display: 'StreetDance', checked: false},
    { id: '1', display: 'Modern', checked: false},
    { id: '2', display: 'Ballet', checked: false},
    { id: '3', display: 'Jazz', checked: false},
  ];

  id: any;

  constructor(
    private crewService:CrewService,
    private router: Router,
    private firebaseService: FirebaseService,
    private route: ActivatedRoute,
    private af: AngularFire,

  ) {}

  ngOnInit() {
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.userin = firebase.auth().currentUser.email;
     }
     return this.userin;
    });
        // Get Current Profile to Display
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.userin) {
          this.userUid = profiles[i].$key;
          this.userEmail = profiles[i].username;
          this.userUrl = profiles[i].url;
          this.userClassCount = profiles[i].classes;
          // this.supportingAdd = profiles[i].supportingAdd;
        }
      }
    });
             // Get Classes to Display
    this.crewService.getClasses().subscribe((classes) => {
      this.classes = classes;

    });

  }

  onAddSubmit() {

    let classes = {
      userUid: this.userin,
      userin: this.userEmail,
      studio: this.studio,
      level: this.level,
      phone: this.phone,
      city: this.city,
      age: this.age,
      location: this.location,
      title: this.title,
      description: this.description,
      schedules: this.schedules,
      participants: this.participants,
      requests: false,
      members: false,
      requestCount: this.participants,
      styles: this.styles,
      date: this.date,
      url: this.userUrl
    }
    let classCount = {
      classes: this.userClassCount + 1
    }


    this.crewService.addClass(classes);
    this.firebaseService.updateProfile(this.userUid, classCount)
    this.router.navigate([this.userUid+'/classes/'+this.userUid]);

  }
}



