import { Component, OnInit } from '@angular/core';
import { AngularFire } from 'angularfire2';
import {FirebaseService} from '../../../../services/firebase.service';
import {Router, ActivatedRoute, Params} from '@angular/router';
import * as firebase from 'firebase';

@Component({
  selector: 'app-add-video',
  templateUrl: './add-video.component.html',
  styleUrls: ['./add-video.component.sass']
})
export class AddVideoComponent implements OnInit {
  zeals: any[];
  userUrl: any;
  durations: any[]; // Get create time
  videoKey: any; // get clicked video key
  duration: any; // get length of video
  currentUser: any; // get current email
  time: boolean = false; // check if time lenght of video
  userUid: any; // current key
  id: any; // get current url id
  profiles: any; // get profiles

  // Video form info
  name: any;
  description: any;
  count: number = 0;
  level: string = 'Beginner';
  booLean: boolean = true;
  videos: Video[];
  video: any;
  username: any;
  date: Date = new Date();
  styles = [ // Check Styles of profile
      { id: '0', display: 'StreetDance', checked: false},
      { id: '1', display: 'Modern', checked: false},
      { id: '2', display: 'Ballet', checked: false},
      { id: '3', display: 'Jazz', checked: false},
    ];
  constructor(
    private firebaseService:FirebaseService,
    private router: Router,
    private route: ActivatedRoute,
    private af: AngularFire,

  ) {
  }

  ngOnInit() {

  // Get Current Profile
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.username = profiles[i].username;
          this.userUid = profiles[i].$key;
          this.userUrl = profiles[i].url

        }
      }
    });
    // Get Videos
    this.firebaseService.getVideos().subscribe((videos) => {
      this.videos = videos;
    });
    // Get Zeals
    this.firebaseService.getZeals().subscribe((zeals) => {
      this.zeals = zeals;
    });
    // Get Video Duration
    this.firebaseService.getDurationVideo(this.userUid).subscribe((videoDuration) => {
      this.durations = videoDuration;

    });
       // Get current email
    this.af.auth.subscribe(auth => {

     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });

  }

  updateUrl() { // Short update to get video duration
    let video = {
      videoDuration: false,
      userUid: this.userUid,
      name: this.name

    }
    this.firebaseService.addVideo(video); // push video to get time length

  }
  onMetadata(video, videoKey) { // Store info from uploaded video
    this.duration = video.duration;
    this.videoKey = videoKey;
    this.time = true

  }
  onAddSubmit() { // Video form info
    this.router.navigate(['/'+this.userUid]); // Navigate to profile
    let styles = { // add styles
      styles: this.styles
    }
    let zeals = {
      addZeal: true,
      zeal: false,
      zeals: this.count,
    }
    let videos = {
      name: this.name,
      champion: this.level,
      description: this.description,
      userUid: this.userUid,
      userin: firebase.auth().currentUser.email,
      username: this.username,
      duration: this.duration,
      videoDuration: true,
      views: this.count,
      styles: this.styles,
      watch: true,
      watching: this.count,
      date: this.date,
      imgUrl: this.userUrl
    }
    this.firebaseService.updateVideo(this.videoKey, videos); // update video database
    this.firebaseService.updateZeal(this.videoKey, zeals); // update zeals database
    for (var i = 0; i < this.durations.length; i++) { // check if upload failed, remove video duration false
          if(this.durations[i].videoDuration === false) {
              this.firebaseService.deleteVideo(this.durations[i].$key)
          }
      }
  }
}
export interface Video { // Video Interface
    name?: string;
    supporters?: string;
    champion?: boolean;
    support?: any;
    description?: string;
    userin?: any;
    userId?: any;
    username?: any;
}



