import { Component, OnInit } from '@angular/core';
import { FirebaseService } from "app/services/firebase.service";
import { AngularFire } from "angularfire2";
import * as firebase from 'firebase';
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-video-manager',
  templateUrl: './video-manager.component.html',
  styleUrls: ['./video-manager.component.sass']
})
export class VideoManagerComponent implements OnInit {
  userUid: any;
  profiles: any[];
  video: any;
  profile: any;
    id: any;
    currentUser: string;
    videos: any[];

  constructor(
      private firebaseService: FirebaseService, 
      private af: AngularFire,
      private route:ActivatedRoute
  ) { }

  ngOnInit() {
    // Get id to fetch the user details
    this.id = this.route.snapshot.params['username'];
      // Get Current Profile to Display
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.userUid = profiles[i].$key;

        }
      }
    });
    // Store the current user
    this.af.auth.subscribe(auth => {
      
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });
    // Get Videos to Display
    this.firebaseService.getVideos().subscribe((videos) => {
      this.videos = videos;
    });
     // Get Videos to Display
    this.firebaseService.getVideoDetails(this.id).subscribe((video) => {
      this.video = video;
    });

  }
}



