import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';

import { AngularFire } from 'angularfire2';
import * as firebase from 'firebase';
import { FirebaseService } from "app/services/firebase.service";

@Component({
  selector: 'app-supporting',
  templateUrl: './supporting.component.html',
  styleUrls: ['./supporting.component.sass']
})
export class SupportingComponent implements OnInit {

  supporters:any;
  id:any;
  currentUser: any; // fetch current user name

  constructor(
    private af: AngularFire,
    private firebaseService: FirebaseService,
    private route:ActivatedRoute
  ) { }

  ngOnInit() {
    // Get id to fetch the user details
    this.id = this.route.snapshot.params['username'];
    this.firebaseService.getSupporters().subscribe(supporters => {
      this.supporters = supporters
    })
    // Store the current user
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
    });


  }
}



