import { Injectable } from '@angular/core';
import { AngularFire, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2';
import * as firebase from 'firebase';

@Injectable()
export class CrewService {
  classes: FirebaseListObservable<any[]>;
  class: FirebaseObjectObservable<any>;

  constructor(private af: AngularFire) {
  }
   // Classes Methods
  getClasses(){
    this.classes = this.af.database.list('/classes') as FirebaseListObservable<Class[]>
    return this.classes;
  }
  getClassRequests(id){
    this.classes = this.af.database.list('/classes/'+id+'/requests') as FirebaseListObservable<Class[]>
    return this.classes;
  }
  getClassMembers(id){
    this.classes = this.af.database.list('/classes/'+id+'/members') as FirebaseListObservable<Class[]>
    return this.classes;
  }
   getClass(id){
    this.classes = this.af.database.list('/classes/', {
      query: {
        orderByChild: 'userin',
        equalTo: id
      }
    }) as FirebaseListObservable<Class[]>
    return this.classes;
  }
   getClassDetails(id){
    this.class = this.af.database.object('/classes/'+id) as FirebaseObjectObservable<Class>
    return this.class;
  }
  getClassDetailsSchedules(id){
    this.class = this.af.database.object('/classes/'+id+'/schedules/') as FirebaseObjectObservable<Class>
    return this.class;
  }

  addClass(classes){
    return this.classes.push(classes);
  }
  updateClass(id, classes){
    this.getClasses()
    return this.classes.update(id, classes);
  }
  deleteClass(id){
    return this.classes.remove(id);
  }
}
interface Class{
  $key?:string;
  title?: string;
  discription?: string;
  monday?: number;
  tuesday?: number;
  wednesday?: number;
  thursday?: number;
  friday?: number;
  saturday?: number;
  sunday?: number;
  image?:string;
  userin?:any;
  date?:number
}


