import {RouterModule, Routes} from '@angular/router';

import { AppComponent } from './app.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';
import { AddInfoComponent } from './components/add-info/add-info.component';
import { ContestsComponent } from './components/contests/contests.component';
import { AddVideoComponent } from './components/profile/videos/add-video/add-video.component';
import { EditVideoComponent } from './components/profile/videos/edit-video/edit-video.component';
import { EditVideosComponent } from './components/profile/videos/edit-video/edit-videos.component';
import { EditProfileComponent } from './components/profile/edit-profile/edit-profile.component';
import { SupportersComponent } from './components/profile/supporters/supporters.component';
import { SupportingComponent } from './components/profile/supporting/supporting.component';
import { BrowseComponent } from './components/browse/browse.component';
import { VideoManagerComponent } from './components/video-manager/video-manager.component';

import { LoginComponent } from './components/login/login.component';
import { SignUpComponent } from './components/login/sign-up/sign-up.component';
import { CrewComponent } from './components/crew/crew.component';
import { ClassesProfileComponent } from './components/profile/classes/classes.component';
import { VideosComponent } from './components/profile/videos/videos.component';
import { AddClassesComponent } from './components/profile/classes/add-classes/add-classes.component';
import { ClassesComponent } from './components/classes/classes.component';
import { ClassesManagerComponent } from './components/classes-manager/classes-manager.component';
import { EditClassComponent } from './components/profile/classes/edit-class/edit-class.component';
import { EditClassesComponent } from './components/profile/classes/edit-class/edit-classes.component';
import { RequestsComponent } from './components/classes/requests/requests.component';
import { MembersComponent } from './components/classes/members/members.component';

import { ShortClassesComponent } from './components/profile/classes/short-classes/short-classes.component';
import { BrowseVideosComponent } from './components/browse/browse-videos/browse-videos.component';
export const routes: Routes = [
  {path:'', component: DashboardComponent },
  {path:'add', component: AddInfoComponent },
  {path:'login', component: LoginComponent },
  {path:'signup', component: SignUpComponent },
  {path:'crews', component: CrewComponent },
  {path:'browse/:profile/:username', component: BrowseVideosComponent },
  {path:'browse', component: BrowseComponent },
  {path:'classes', component: ClassesComponent },
  {path:'contests', component: ContestsComponent },
  {path:'classes/add', component: AddClassesComponent },
  {path:'class/requests/:username', component: RequestsComponent },
  {path:'class/members/:username', component: MembersComponent },
  {path:'supporting/:username', component: SupportingComponent },
  {path:'supporters/:username', component: SupportersComponent },
  {path:'video-edit/:username', component: EditVideoComponent },
  {path:'edit/:username', component: EditProfileComponent },
  {path:'edit-class/:username', component: EditClassComponent },
  {path:'classes/:username', component: ShortClassesComponent },
  {path:'classes/:username/:username', component: ClassesProfileComponent },
  {path:'video-manager/:username', component: VideoManagerComponent,
    children: [
        {path:'', component: EditVideosComponent },
        {path:'add', component: AddVideoComponent },
        { path: '', redirectTo: '', pathMatch: 'full' },
        { path: '**', redirectTo: '', pathMatch: 'full' },
    ]
  },
  {path:':profile/:username', component: VideosComponent },
  {path:'classes-manager/:username', component: ClassesManagerComponent,
    children: [
        {path:'', component: EditClassesComponent },
        {path:'add', component: AddClassesComponent },
        { path: '', redirectTo: '', pathMatch: 'full' },
        { path: '**', redirectTo: '', pathMatch: 'full' },
    ]
  },

  {path:':username', component: ProfileComponent },
  {path: '', redirectTo: '', pathMatch: 'full' },
  {path: '**', redirectTo: '', pathMatch: 'full' },
];

export const appRoutingProviders: any[] = [

];

export const routing = RouterModule.forRoot(routes);



