import { Component, OnInit } from '@angular/core';
import { CrewService } from "app/services/crew.service";
import { AngularFire } from 'angularfire2';
import {FirebaseService} from '../../../services/firebase.service';
import {Router, ActivatedRoute, Params} from '@angular/router';
import * as firebase from 'firebase';

@Component({
  selector: 'app-requests',
  templateUrl: './requests.component.html',
  styleUrls: ['./requests.component.sass']
})
export class RequestsComponent implements OnInit {
  requestCount: any;
  userUid: any;
  profiles: any[];
  currentUser: string;
  participants: any;
  classes: any;
  id: any;

  constructor(
      private crewService: CrewService,
      private firebaseService:FirebaseService,
      private router: Router,
      private route: ActivatedRoute,
      private af: AngularFire,
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params['username'];
    this.crewService.getClassRequests(this.id).subscribe(classes => {
        this.classes = classes;

    })
    this.crewService.getClassDetails(this.id).subscribe(cls => {
          this.participants = cls.participants
          this.requestCount = cls.requestCount
    })
      // Store the current user
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });
         // Get Current Profile to Display
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.userUid = profiles[i].$key;


        }
      }
    });
  }
  acceptRequest(key) {
    let accept = {
      requestCount: this.requestCount - 1,
      participants: this.participants + 1,

    }
    let members = {
      member: key,
    }
    let requestsDefault = {
      requests: false
    }
    this.crewService.updateClass(this.id, accept);
    this.crewService.updateClass(this.id+'/members/'+key, members);
    if(this.requestCount < 1) {
        this.crewService.updateClass(this.id, requestsDefault);
    } else {
      this.crewService.deleteClass(this.id+'/requests/'+key);
    }
  }
  declineRequest(key) {
    let requestsDefault = {
      requests: false
    }
     let refuse = {
      requestCount: this.requestCount - 1,
    }
    this.crewService.updateClass(this.id, refuse);
    if(this.requestCount < 1) {
      this.crewService.updateClass(this.id, requestsDefault);
    } else {
        this.crewService.deleteClass(this.id+'/requests/'+key);
    }
  }

}



