import { Component, OnInit } from '@angular/core';
import { FirebaseService } from "app/services/firebase.service";
import { AngularFire } from "angularfire2";
import * as firebase from 'firebase';
import { ActivatedRoute } from "@angular/router";
import { CrewService } from "app/services/crew.service";

@Component({
  selector: 'app-edit-classes',
  template: `
            <div class="row bg-grey" *ngIf="profile?.userin === currentUser">
            <div class="container-fluid">
                <div *ngFor="let class of classes">
                    <div *ngIf="class?.userin === profile?.$key">
                        <div class="col-md-3 bg-white cl-black">
                            <div class="row addMargDown">
                                <div class="col-md-2 addMarg">
                                    <img class="img" src="{{class?.url}}" border="0">
                                </div>
                                <div class="row">
                                    <div class="col-md-8 addMargSec">
                                        <p>Instructor:&nbsp; <a class="cl-purple" [routerLink]="['/'+class?.userin]">{{class.userin}}</a></p>

                                    </div>
                                    <div class="col-md-8">
                                        <h2>{{class.title | truncate : 57}}</h2>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-md-12 inline-block">
                                    <p class="cl-purple">@{{class.studio}} &nbsp; | &nbsp;</p>
                                    <p>{{class.location}}</p>
                                </div>
                            </div>
                            <p>{{class.description | truncate : 230}}</p>
                            <div class="inline-block">
                                <p>Styles: &nbsp; </p>
                                <div *ngFor="let cls of class.styles" class="inline-block">
                                    <p class="cl-purple">#{{cls?.display | lowercase}} &nbsp;</p>
                                </div>
                            </div>
                            <div class="inline-block">
                                <p>Level: &nbsp; {{class?.level}} &nbsp; | &nbsp;</p>
                                <p>Age: &nbsp; {{class?.age}}</p>

                            </div>
                            <div>
                                <button class="btn btn-danger pull-right" [routerLink]="['/edit-class/'+class?.$key]">Edit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            `,
  styleUrls: ['./edit-class.component.sass']
})
export class EditClassesComponent implements OnInit {
    classes: any[]; // get classes
    profile: any; // get profile from url
    id: any; // get profile info from url
    currentUser: string; // get current email

  constructor(
      private firebaseService: FirebaseService,
      private crewService: CrewService,
      private af: AngularFire,
      private route:ActivatedRoute
  ) { }

  ngOnInit() {
    // Get id to fetch the user details
    this.id = this.route.snapshot.params['username'];
    // Get current email
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });
    // Get Classes to Display
    this.crewService.getClasses().subscribe((classes) => {
      this.classes = classes;
    });
    // Get Profile Details
    this.firebaseService.getProfileDetails(this.id).subscribe(profile => {
      this.profile = profile;
      });
  }

}



