import { Component, OnInit } from '@angular/core';
import { AngularFire } from 'angularfire2';
import {FirebaseService} from '../../services/firebase.service';
import {Router, ActivatedRoute, Params} from '@angular/router';
import * as firebase from 'firebase';
import {FormsModule} from '@angular/forms'

@Component({
  selector: 'app-add-info',
  templateUrl: './add-info.component.html',
  styleUrls: ['./add-info.component.sass']
})
export class AddInfoComponent implements OnInit {
  uid: string;
  promo: any;
  promoUniq: any; // promo code profile info
  profiles: any[];  // get profiles
  users?: any; // get users
  id: any; // get id from params
  // Form info
  title: any;
  username: any;
  description: any;
  level: string = 'Beginner';
  userin: any;
  image: any;
  video: any;
  count: number = 0;
  support: any;
  sub: boolean = true;
  styles = [ // Check Styles of profile
    { id: '0', display: 'StreetDance', checked: false},
    { id: '1', display: 'Modern', checked: false},
    { id: '2', display: 'Ballet', checked: false},
    { id: '3', display: 'Jazz', checked: false},
  ];

  constructor(
    private firebaseService:FirebaseService,
    private router: Router,
    private route: ActivatedRoute,
    private af: AngularFire,

  ) {}

  ngOnInit() {
    // Get current email
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.userin = firebase.auth().currentUser.email;
     }
     return this.userin;
    });
    // Get current uid
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.uid = firebase.auth().currentUser.uid;
     }
     return this.uid;
    });
    // Get current users
    this.firebaseService.getUsers().subscribe(users => {
      this.users = users;
    })
    // Get current profiles
    this.firebaseService.getProfiles().subscribe(profiles => {
      this.profiles = profiles;
    })

  }
  checkPromo(promo) { // Get promo code profile info
    this.firebaseService.getProfilePromo(promo).subscribe(profiles => {
      this.promoUniq = profiles[0];
    })
  }
  onAddSubmit() { // Submit form info
    let referralInfo = { // Profile info of user that created account with promo
      username: this.username,
      userin: this.userin
    }
    let referral = { // Keeping track of how many refferals each user have
      referralCount: this.promoUniq.referralCount + 1,
    }
    let user = { // Create Profile
      username: this.username,
      userin: this.userin

    }
    let styles = { // add styles
      styles: this.styles
    }

    let profile = { // Form object
      level: this.level,
      username: this.username,
      displayName: this.username,
      description: this.description,
      supporters: this.count,
      supportingAdd: this.count,
      sub: this.sub,
      userin: this.userin,
      support: this.sub,
      styles: this.styles,
      referralCount: this.count,
      promo: firebase.database().ref().child('profiles').push().key,
      views: this.count,
      classes: this.count,
    }

    this.firebaseService.updateUser(this.uid, user); // Push user
    this.firebaseService.addProfile(this.username, profile); // Update profile database, create profile
    this.firebaseService.updateProfile(this.promoUniq.$key, referral); // Update refferal count of user with the promo code
    this.firebaseService.updateProfile(this.promoUniq.$key+'/referral/'+this.username, referralInfo); // Update refferal count of user with the promo code
    this.router.navigate(['/']); // Navigate back dashboard

  }

}



