import { Component, OnInit } from '@angular/core';
import { CrewService } from "app/services/crew.service";
import { AngularFire } from 'angularfire2';
import {FirebaseService} from '../../../../services/firebase.service';
import {Router, ActivatedRoute, Params} from '@angular/router';
import * as firebase from 'firebase';

@Component({
  selector: 'app-edit-class',
  templateUrl: './edit-class.component.html',
  styleUrls: ['./edit-class.component.sass']
})
export class EditClassComponent implements OnInit {
  class: any;  // get class details
  currentUser: any; // get current email
  id: any; // get current key of url
  profiles: any; // get profiles
  userUid: any; // get current key
  // Update form info
  title;
  studio;
  username;
  description;
  level;
  age;
  location;
  phone;
  schedules = [ // Class Schedule
    { display: 'Monday', from: '', to: ''},
    { display: 'Tuesday', from: '', to: ''},
    { display: 'Wednesday', from: '', to: ''},
    { display: 'Thursday', from: '', to: ''},
    { display: 'Friday', from: '', to: ''},
    { display: 'Saturday', from: '', to: ''},
    { display: 'Sunday', from: '', to: ''},

  ];

  constructor(
    private crewService: CrewService,
    private firebaseService:FirebaseService,
    private router: Router,
    private route: ActivatedRoute,
    private af: AngularFire,
  ) { }

  ngOnInit() {
    // Get current profile key from url
    this.id = this.route.snapshot.params['username'];
    // Get current email
    this.af.auth.subscribe(auth => {
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });
    // Get Current Profile
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.userUid = profiles[i].$key;
        }
      }
    });
    // Get class info for edit
    this.crewService.getClassDetails(this.id).subscribe(cls => {
      this.class = cls;
      this.title = cls.title;
      this.studio = cls.studio;
      this.username = cls.username;
      this.description = cls.description;
      this.level = cls.level;
      this.age = cls.age;
      this.location = cls.location;
      this.phone = cls.phone;
      this.schedules = cls.schedules;
      })
  }
    onEditSubmit() { // Update class form
      let classes = {
        title: this.title,
        studio: this.studio,
        description: this.description,
        level: this.level,
        age: this.age,
        location: this.location,
        phone: this.phone,
        schedules: this.schedules
      }

      this.crewService.updateClass(this.id, classes); // update class
      this.router.navigate(['/classes-manager/'+this.userUid]); // navigate to class manager
  }
  onDelete() {
    this.router.navigate(['/classes-manager/'+this.userUid]); // navigate to class manager
    this.crewService.deleteClass(this.id); // deelte class
  }

}



