import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import * as firebase from 'firebase';

import { FirebaseService} from './services/firebase.service';
import { AngularFire, AuthProviders } from 'angularfire2';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.sass']
})
export class AppComponent implements OnInit {
  username: any;
  userUid: any;
  users: any;
  status = true;
  profiles: any;
  currentUser: any;
  
  toggle(status) {
    this.status = !status;
  }
  constructor(public af:AngularFire, private firebaseService:FirebaseService, private router:Router) {
  }
  ngOnInit(){
    
     // Get Current Profile to Display
    this.firebaseService.getProfiles().subscribe((profiles) => {
      this.profiles = profiles;
      for (var i = 0; i < profiles.length; i++) {
        if(profiles[i].userin === this.currentUser) {
          this.userUid = profiles[i].$key;
          this.username = profiles[i].username;
        }
      }
    });

    // Store the current user
    this.af.auth.subscribe(auth => {
      
     if(auth) {
       this.currentUser = firebase.auth().currentUser.email;
     }
     return this.currentUser;
    });

  }
  onClick() {
    this.router.navigate(['./'+this.userUid],{skipLocationChange: true});
  }
  login(){
    this.af.auth.login();
    this.router.navigate(['/'])

  }
  
  logout(){
    this.router.navigate(['/'])
    this.currentUser = ''
    this.af.auth.logout();
  }
}



